.App {
  text-align: center;
  height: calc(100vh);
  min-height: calc(100vh);
  max-height: calc(100vh);
  color: var(--text);
}

.App-header {
  color: var(--text);
  height: calc(30vh);
}

.App-title {
  color: var(--accent);
  font-size: 3em;
}

.App-intro {
  font-size: large;
}




